* {
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  -webkit-user-select: none;
  user-select: none;
}

body {
  height: 100%;
  width: 100%;
  background-color: #f0f1fa;
  font-family: Varela Round, sans-serif;
  font-size: 16px;
}

h1 {
  color: #686e8f;
  font-size: 64px;
}

.content {
  height: calc(100vh - 50px);
  width: 100vw;
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.content .container {
  width: 1000px;
  flex-direction: column;
  align-items: center;
  row-gap: 50px;
  display: flex;
}

.content .container .title {
  width: 86%;
  height: 100px;
  justify-content: space-between;
  align-items: center;
  margin: 30px 0 0;
  padding: 0 0 0 10px;
  display: flex;
}

.content .container .title .display-banner {
  height: 100%;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  display: flex;
}

.content .container .title .display-banner .display-scores {
  text-align: center;
  width: 100px;
  height: 100%;
  text-transform: uppercase;
  color: #f5f5f5;
  background-color: #ea596e;
  border-radius: 3px;
  flex-direction: column;
  justify-content: center;
  row-gap: 10px;
  font-size: 18px;
  display: flex;
}

.content .container .game {
  width: 80%;
  height: 700px;
  background-color: #686e8f;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  padding: 20px;
  display: flex;
}

.content .container .game .poke-tile-set {
  width: 600px;
  height: 100%;
  overflow: none;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.content .container .game .poke-tile-set table {
  width: 600px;
  height: 600px;
  box-shadow: inset 0 0 10px #000;
}

.content .container .game .poke-tile-set .time-tracker {
  width: 100%;
  height: 20px;
  background-color: #dadada;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: inset 0 0 10px #0005;
}

.content .container .game .poke-tile-set .time-tracker .fill {
  height: 100%;
  width: 0%;
  background-color: #51a8ff;
}

@keyframes decrease-fill {
  from {
    width: 100%;
  }

  to {
    width: 0%;
  }
}

.content .container .game .poke-tile-set table tr td {
  text-align: center;
  width: 50%;
  height: 50%;
  background-color: #ffbbbb7e;
  padding: 0;
  font-size: 24px;
  animation: .5s font-grow;
}

.content .container .game .poke-img-set {
  height: 680px;
  width: 160px;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  row-gap: 10px;
  padding-top: 10px;
  transition: all 1s;
  display: flex;
}

.poke-img {
  background-color: #fff;
  transition: all .5s;
}

.poke-img.hidden {
  height: 160px;
  width: 160px;
  box-shadow: 0 8px 16px #0003;
}

.poke-img.shown {
  height: 100%;
  width: calc(100%x);
  margin-left: 0;
  animation: .5s grow;
}

.poke-img img {
  height: 100%;
  animation: .5s grow;
}

.poke-img.hidden img {
  filter: contrast(0);
}

.poke-img.shown img {
  filter: contrast();
  animation-delay: 1s ease;
  background-color: #78c878;
  animation: 1s green-bg-color-change !important;
}

.pre-game-modal {
  width: 100%;
  height: 100vh;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.pre-game-modal .content {
  width: 1200px;
  height: 180px;
  pointer-events: all;
  color: #fff;
  background-color: #ea596e;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  font-weight: 800;
  display: flex;
}

@keyframes font-grow {
  from {
    font-size: 16px;
  }

  to {
    font-size: 24px;
  }
}

@keyframes grow {
  from {
    transform: scale(.8);
  }

  to {
    transform: scale(1);
  }
}

@keyframes green-bg-color-change {
  from {
    background-color: #fff;
  }

  to {
    background-color: #78c878;
  }
}

.shake {
  background-color: #ff6262;
  animation: .5s infinite shake;
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px)rotate(0);
  }

  10% {
    transform: translate(-1px, -2px)rotate(-1deg);
  }

  20% {
    transform: translate(-3px)rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px)rotate(0);
  }

  40% {
    transform: translate(1px, -1px)rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px)rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px)rotate(0);
  }

  70% {
    transform: translate(3px, 1px)rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px)rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px)rotate(0);
  }

  100% {
    transform: translate(1px, -2px)rotate(-1deg);
  }
}

.shimmer {
  background-repeat: no-repeat;
  font-size: 50px;
  animation: 1s infinite shimmer;
  -webkit-mask: linear-gradient(-60deg, #000 30%, #0005, #000 70%) 100% / 300% 100%;
}

@keyframes shimmer {
  100% {
    -webkit-mask-position: 0;
  }
}

